@charset "UTF-8";

// Note: Using @import for compatibility with older Jekyll/SASS
// SASS modules are available through _sass_compat.scss if needed
@import "_sass_compat";
@import "_mixins";

// Variables
@import "components/_variables";

// Reset
@import "components/_normalize";

// Components
@import "components/_global";
@import "components/_helpers";
@import "components/_grid";
@import "components/_typography";
@import "components/_buttons";
@import "components/_syntax-highlighting";

// Pages styling
@import "pages/_layout";
@import "pages/_tags";
@import "pages/_archive";
@import "pages/_post";

// Accessibility - Skip to main content link
.skip-link {
  position: absolute;
  left: -9999px;
  z-index: 999;
  padding: 8px;
  background: #000;
  color: #fff;
  text-decoration: none;

  &:focus {
    left: 10px;
    top: 10px;
  }
}
